/**
 * Styles for the search block
 */
.search {
    left: 0;
    position: fixed;
    right: 0;
    top: 0;
    z-index: 1;
    
    height: 100px;

    background: $color-primary;
    opacity: 0;

    transition: opacity 0.25s ease,
                visibility 0.25s ease 0.25s;
    visibility: hidden;

    &__form {
        align-items: center;
        display: flex;
        height: 100%;
        margin: 0 auto;
        max-width: 1200px;
        padding: 0 10px;
    }

    &__input {
        flex: 1 1;

        color: $color-concrete;
        font-family: $font-family-regular;
        @include font-size(12px);
        letter-spacing: 0.2em;

        background: transparent;
        border: none;
        outline: none;

        &::placeholder {
            color: $color-silver-sand;
            text-transform: uppercase;
        }
    }

    &__submit {
        color: $color-white;
        @include font-size(16px);

        background: transparent;
        border: none;
        cursor: pointer;
        opacity: 0.7;
        outline: none;

        transition: opacity 0.25s ease;

        &:hover,
        &:focus {
            opacity: 1;
        }

        &:focus {
            outline: 1px dotted;
        }
    }

    &__close {
        position: relative;

        display: block;
        height: 22px;
        margin-left: 20px;
        padding: 3px;
        width: 22px;

        color: $color-white;
        text-indent: -9999px;

        background: transparent;
        border: none;
        cursor: pointer;
        opacity: 0.7;
        outline: none;

        transition: opacity 0.25s ease;

        &:before,
        &:after {
            content: "";

            left: 0;
            top: 50%;
            position: absolute;

            display: block;
            margin-top: -2px;
            height: 1px;
            width: 22px;

            background: $color-white;
        }

        &:before {
            transform: rotate(45deg);
        }

        &:after {
            transform: rotate(-45deg);
        }

        &:hover,
        &:focus {
            opacity: 1;
        }

        &:-moz-focusring {
            outline: 1px dotted;
        }
    }

    &_visible {
        opacity: 1;

        transition-delay: 0s;
        visibility: visible;
    }
}
